<i18n>
{
  "en": {
    "all": "All",
    "charger": "Charger",
    "battery": "Battery",
    "QRcode": "Scan QR code",
    "commonProblems": "Common problems",
    "instructions": "Instructions",
    "contactCustomer": "Contact customer service",
    "cancel": "Cancel",
    "EquipmentOffLine": "Equipment off-line",
    "changeEquipment": "The equipment can not be used for the time being. Please change the equipment.",
    "OK": "OK",
    "Reminder": "Reminder",
    "waitForEnd": "Charging is currently in use. Please wait for the end of the charge to try again.",
    "reScanQRcode": "The page has expired, please re-scan the cabinet qr code",
    "credit": "Sorry, your credit is running low",
    "rechargeTheBalance": "In order to normally use the filling charge, please recharge the balance first.",
    "RechargeBalance": "Recharge balance"
  },

  "zh": {
    "all": "全部",
    "charger": "座充",
    "battery": "充电宝",
    "QRcode": "扫码充电",
    "commonProblems": "常见问题",
    "instructions": "使用说明",
    "contactCustomer": "联系客服",
    "cancel": "取消",
    "EquipmentOffLine": "设备离线",
    "changeEquipment": "该设备暂时不能使用，请换台设备使用",
    "OK": "好的",
    "Reminder": "提示",
    "waitForEnd": "当前已经在使用座充充电，请等待结束充电后，再重试",
    "reScanQRcode": "页面已超时，请重新扫描机柜二维码",
    "credit": "余额不足",
    "rechargeTheBalance": "为了正常使用座充，请先充值余额",
    "RechargeBalance": "充值余额"
  }
}
</i18n>
<template>
  <div class="home">
    <header class="bomPixel">
      <div class="statusBar"></div>
      <div class="nav-left" @click="openSidebar(true)">
        <img src="../../assets/home_images/Head@3x.png">
      </div>
      <div class="nav-center">
        <ul>
          <li @click="navigation(1)" :class="{activeColor:navActive===1}">
            {{ $t('all') }}
            <img v-show="navActive===1" src="../../assets/home_images/sliding@3x.png">
          </li>
          <li @click="navigation(2)" :class="{activeColor:navActive===2}">
            {{ $t('charger') }}
            <img v-show="navActive===2" src="../../assets/home_images/sliding@3x.png">
          </li>
          <li @click="navigation(3)" :class="{activeColor:navActive===3}">
            {{ $t('battery') }}
            <img v-show="navActive===3" src="../../assets/home_images/sliding@3x.png">
          </li>
        </ul>
      </div>
      <div class="nav-right"  @click="openStoreSort">
        <img src="../../assets/home_images/seach@3x.png">
      </div>
    </header>
    <div class="home-container" :class="{filterClass: payTip===1||payTip===2||payTip===3}">
      <!-- 地图 -->
      <map-module ref="AMap"
                  :detailData.sync="detailData"
                  :showDetail.sync="showDetail">
      </map-module>
      <div class="button">
        <img class="btn" src="../../assets/home_images/btn.png">
        <span @click="bomHandle(1)">
          <img src="../../assets/home_images/scan.png">
          <em>{{ $t('QRcode') }}</em>
        </span>
        <div class="positioning" @click="bomHandle(2)">
          <img src="../../assets/home_images/restoration.png">
        </div>
        <div class="service" @click="bomHandle(3)">
          <img src="../../assets/home_images/service.png">
        </div>
      </div>

      <!-- 店铺信息 -->
      <store-column v-if="showDetail"
                    :detailData.sync="detailData"></store-column>

      <!-- 充电时长框 -->
      <charging-time :chargingTime="chargingTime"
                     :chargingTimeValue="chargingTimeValue"></charging-time>
    </div>

    <!-- 侧边栏 -->
    <sidebar :sidebarShow="sidebarShow"
             @openSidebar="openSidebar"></sidebar>

    <!-- 座充扫码 -->
    <time-column :timePicker="timePicker"
                 @timeHandle="timeHandle"></time-column>

    <money-tip  :moneyTip="moneyTip"
                :totalAmount="totalAmount"
                @moneyHandle="moneyHandle"></money-tip>

    <!-- 弹框 -->
    <message-box  :id="1"
                  icon="offLine"
                  :title="$t('EquipmentOffLine')"
                  :describe="$t('changeEquipment')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <message-box  :id="2"
                  icon="battery"
                  :title="$t('Reminder')"
                  :describe="$t('waitForEnd')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <message-box  :id="3"
                  icon="scan"
                  :title="$t('Reminder')"
                  :describe="$t('reScanQRcode')"
                  :alert="$t('OK')"
                  @boxHandle="boxHandle"></message-box>

    <message-box  :id="4"
                  icon="defic"
                  :title="$t('credit')"
                  :describe="$t('rechargeTheBalance')"
                  :alert="$t('RechargeBalance')"
                  @boxHandle="boxHandle"></message-box>

    <!-- 操作表 -->
    <action-sheet :id="1"
                  :title="$t('commonProblems')"
                  :list="[$t('instructions'), $t('contactCustomer')]"
                  :cancel="$t('cancel')"
                  color="#575757"
                  @sheetHandle="sheetHandle"></action-sheet>

    <!-- 支付提示框 -->
    <pay-tip :payTip="payTip"></pay-tip>

    <transition name="fade-effect" mode="in-out">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { numToTimeFormat } from '../../utils/index.js'
import sidebar from '../sidebar/index'
import mapModule from './components/amap/index'
import storeColumn from './components//StoreColumn'
import timeColumn from '../scanning/scanCode/timeColumn'
import moneyTip from '../scanning/scanCode/money'
import messageBox from '../../components/MessageBox/index'
import actionSheet from '../../components/ActionSheet/index'
import payTip from './components/payTip'
import chargingTime from './components/chargingTime'
import {setStorage} from '../../utils/local'
export default {
  name: 'home',
  data () {
    return {
      navActive: 1, // 头部导航选择
      timePicker: false, // 时间选择框
      totalTime: 60, // 总时间
      moneyTip: false, // 金额提示框
      totalAmount: 0, // 总金额
      payTip: 0, // 支付提示框  0、关闭 1、正在支付 2、支付完成 3、成功归还支付宝
      chargingTime: false, // 剩余充电时长框
      showDetail: false, // 店铺详情
      detailData: {} // 店铺详情数据
    }
  },

  components: {
    sidebar,
    mapModule,
    storeColumn,
    messageBox,
    timeColumn,
    moneyTip,
    payTip,
    chargingTime,
    actionSheet
  },

  computed: {
    ...mapGetters([
      'sidebarShow', 'isLogin', 'chargerId', 'userInfo', 'globalMessageBox', 'messageBoxID', 'mqttState', 'scanFlag'
    ]),
    chargingTimeValue () {
      return numToTimeFormat(this.totalTime)
    }
  },

  mounted () {
    console.log(this.scanFlag)
    // setTimeout(() => {
    //   console.log(this.detailData)
    // }, 10000)
    this.DATA_RESPONSE()

    // 查询系统语言请求
    this.dataInteractionRequest({
      msgType: '0x5041'
    })

    // 检验是否登录请求
    this.dataInteractionRequest({
      msgType: '0x5101'
    })

    if (this.ISDEBUG) {
      let str = '{"msgType":"0x5152","result":true,"data":{"isHavePwd":false,"userName":"Ken","birthday":"","province":"","city":"","town":"","address":"","nickName":"Ken","headPic":"http://img1.imgtn.bdimg.com/it/u=1571269943,663839233&fm=27&gp=0.jpg","sex":"","firstName":"","lastName":"","email":"","mobile":""}}'
      window.dataInteractionResponse(str)
      let str1 = '{"msgType":"0x5222","result":true,"data":{"type":1,"page":1,"count":1,"total_page":1,"data":[{"charger_type":2,"duration":"2小时22分钟","no":"LENT2018120416580222590633934P","pay_state":2,"fee":10,"lent_adress":"中山大道","lent_time":"2018-12-04 16:58:03"}]}}'
      window.dataInteractionResponse(str1)
    }
    if (this.ISDEBUG) {
      window.dataInteractionResponse('{"msgType":"0x5224","result":true,"data":{"no":"LD20190114181143","lent_adress":"广东省广州市天河区建中路3号","lent_time":"2019-01-14 18:12:01","return_time":"2019-01-14 18:12:24","return_adress":"广东省广州市天河区建中路3号","duration":"少于1分钟","fee":100,"pay_state":1,"discount_fee":0,"balance_deduction":100,"need_pay_fee":0}}')
    }
    if (this.ISDEBUG) {
      let str2 = '{"msgType":"0x5226","result":true,"data":{"type":"LENT"}}'
      window.dataInteractionResponse(str2)
    }
  },

  activated () {
    this.DATA_RESPONSE()
  },

  methods: {
    ...mapActions([
      'sidebarSwitch', 'setLogin', 'setChargerId', 'loadingAlert', 'setUserInfo', 'setGlobalBox', 'setGlobalSheet'
    ]),

    // 响应接口
    DATA_RESPONSE () {
      // 归还充电宝界面传值
      if (this.$route.params.payTip === 3) {
        this.payTip = 3
        let timer = setTimeout(() => {
          this.payTip = 0
          clearTimeout(timer)
        }, 2000)

        if (this.ISDEBUG) {
          // let timer2 = setTimeout(() => {
          //   let str = '{"msgType": "0x1205","result":true,"data":{"state": 1}}'
          //   window.dataInteractionResponse(str)
          //   clearTimeout(timer2)
          // }, 4000)
        }
      }
      this.dataInteractionResponse(data => {
        console.log('home')
        console.log(data.msgType)
        switch (data.msgType) {
          case '0x5102':
            // 检验是否登录录返回（0 未登录、1 已登录）
            if (data.result && data.data.state === 1) {
              this.setLogin(true)
              // 查询用户信息请求
              console.log(this.mqttState)
              console.log('mqtt状态')
              let timer = setInterval(() => {
                if (this.mqttState) {
                  clearInterval(timer)
                  this.dataInteractionRequest({
                    msgType: '0x5151'
                  })
                  console.log('sss订单查询')
                  this.dataInteractionRequest({
                    msgType: '0x5221',
                    type: 3,
                    page: 1,
                    count: 10
                  })
                }
              }, 300)
            } else if (data.result && data.data.state === 0) {
              this.setLogin(false)
            }
            break
          case '0x5202':
            // 扫描二维码数据返回
            if (data.result) {
              this.$store.state.user.scanFlag = true
              this.setChargerId(data.data)
              // 查询是否存在未支付单
              this.dataInteractionRequest({
                msgType: '0x5221',
                type: 1,
                page: 1,
                count: 1
              })
              // 查询设备是否在线请求
              // this.dataInteractionRequest({
              //   msgType: '0x5203',
              //   imei: data.data.imei
              // })
            }
            break
          case '0x5204':
            // 查询设备是否在线返回
            if (data.result) {
              if (data.data.isOnline) {
                // 查询押金费率
                this.dataInteractionRequest({
                  msgType: '0x5245'
                })
              } else {
                this.setGlobalBox({bol: true, id: 1})
              }
            }
            break
          case '0x5152':
            // 查询用户信息返回
            if (data.result) {
              this.setUserInfo(data.data)
            }
            break
          case '0x5284':
            // console.log(this.$refs.AMap.storeList(data))
            this.$refs.AMap.storeList(data)
            break
          case '0x5286':
            console.log('this.detailData:', data.data)
            this.showDetail = true
            this.detailData = data.data
            // console.log('this.detailData:', this.detailData)
            break
          case '0x5104': // 点击退出登录在登录时页面不会跳转到home页(全局返回只会执行一个)
          case '0x5108': // 手机号
          case '0x510A': // 支付宝
            this.setUserInfo(data.data)
            this.setLogin(true)
            console.log(this.mqttState)
            console.log('mqtt状态')
            if (this.mqttState) {
              this.dataInteractionRequest({
                msgType: '0x5151'
              })
              console.log('sss订单查询')
              this.dataInteractionRequest({
                msgType: '0x5221',
                type: 3,
                page: 1,
                count: 10
              })
            }
            this.$router.push('/home')
            break
          case '0x5222':
            console.log('查询未支付订单')
            if (data.result) {
              let billInfo = data.data
              console.log(billInfo.data)
              let unpayBill = [] // 未支付订单
              if (billInfo.data) {
                billInfo.data.forEach(element => {
                  if (element.pay_state === 2) {
                    unpayBill.push(element)
                  }
                })
              }
              if (unpayBill.length > 0) {
                // 跳转到支付订单
                this.$router.push({name: 'unpaid', params: {data: data.data}})
              } else {
                if (this.scanFlag) {
                  // 点击扫码返回执行操作
                  this.dataInteractionRequest({
                    msgType: '0x5225'
                  })
                }
              }
              setStorage('unPaybill', unpayBill)
            }
            break
          case '0x5226':
            console.log('查询未归还设备')
            if (data.result) {
              if (data.data.type === 'LENT') {
                this.$store.state.user.unReturndevice = false
                this.dataInteractionRequest({
                  msgType: '0x5203',
                  imei: this.chargerId.imei
                })
                console.log(this.chargerId.imei)
                console.log('机柜id')
              } else if (data.data.type === 'RETURN') {
                this.$store.state.user.unReturndevice = true
                if (this.scanFlag) {
                  this.$router.push('/charger')
                }
              }
            }
            break
          case '0x5224':
            if (data.result) {
              let obj = data.data
              setStorage('biiinfo', obj)
            }
            break
          case '0x5246':
            if (data.result) {
              if (data.data.isDeposit === 1) {
                // 需要押金
                if (data.data.depFee > 0) {
                  this.$router.push('/charger')
                } else {
                  // 押金冲值
                  this.$router.push('/deposit')
                }
              } else if (data.data.isDeposit === 2) {
                // 不需要押金
                this.$router.push('/charger')
              }
            }
            break
          default:
            break
        }
        console.log('ssssss')
      })
    },

    // 头部导航
    navigation (num) {
      this.navActive = num
    },

    // 侧边栏
    openSidebar (state) {
      this.sidebarSwitch(state)
    },

    // 店铺
    openStoreSort () {
      this.$router.push({path: '/store'})
    },

    // 时间选择框
    timeHandle (confirm, time) {
      if (confirm) {
        this.totalTime = parseInt(time[0]) * 60 + parseInt(time[1])
      }

      if (this.ISDEBUG) {
        if (confirm) {
          this.moneyTip = true
          console.log(time)
        }
        this.timePicker = false
      }
    },

    // 金额提示框
    moneyHandle (confirm) {
      if (!this.ISDEBUG) {
        if (confirm) {
        } else {
          this.moneyTip = false
        }
      }

      if (this.ISDEBUG) {
        if (confirm) {
          this.$http('/scanning/1').then(res => {
            if (res.data.result) {
              this.moneyTip = false
              this.payTip = 1
              let timer = setInterval(() => {
                if (++this.payTip > 2) {
                  this.payTip = 0
                  clearInterval(timer)
                  // 剩余充电时长
                  this.chargingTime = true
                }
              }, 2000)
            } else {
              this.moneyTip = false
              this.setGlobalBox({bol: true, id: 4})
            }
          })
        } else {
          this.moneyTip = false
        }
      }
    },

    // 弹框
    boxHandle (confirm, cancel, id) {
      switch (id) {
        case 1:
          this.setGlobalBox({bol: false, id: 0})
          break
        case 2:
          this.setGlobalBox({bol: false, id: 0})
          break
        case 3:
          this.setGlobalBox({bol: false, id: 0})
          break
        case 4:
          this.setGlobalBox({bol: false, id: 0})
          if (confirm) {
            this.$router.push('/recharge')
          }
          break
      }
    },

    // 操作表
    sheetHandle (confirm, value, id) {
      switch (id) {
        case 1:
          break
      }
      this.setGlobalSheet({bol: false, id: 0})
    },

    bomHandle (num) {
      switch (num) {
        // 扫码
        case 1:
          if (!this.isLogin) {
            this.$router.push('/login')
          } else {
            // this.dataInteractionRequest({
            //   msgType: '0x5221',
            //   type: 1,
            //   page: 1,
            //   count: 1
            // })
            if (!this.ISDEBUG) {
              this.loadingAlert(true)
              // 调用摄像头扫描二维码请求
              this.dataInteractionRequest({
                msgType: '0x5201'
              })
            } else {
              this.$http('/scanning/index').then(res => {
                this.setChargerId({'imei': '1000000009', 'merchantId': 114})
                console.log(res.data.type)
                if (res.data.type === 'lease') {
                  // 剩余充电时长
                  this.chargingTime = false
                  this.$router.push({path: '/charger'})
                } else if (res.data.type === 'restitute') {
                  // 剩余充电时长
                  this.chargingTime = false
                  this.$router.push({path: '/charger'})
                } else if (res.data.type === 'scanCode') {
                  this.timePicker = true
                }
              })
            }
          }
          break
        // 定位
        case 2:
          this.$refs.AMap.setCenter()
          break
        // 客服
        case 3:
          this.setGlobalSheet({bol: true, id: 1})
          break
      }
    }
  },

  watch: {
    sidebarShow (newValue) {
      if (!newValue) {
        this.DATA_RESPONSE()
      }
    }
  }
}
</script>

<style scoped lang="less">
.home{
  .wh(100%, 100%);
  position: relative;
  header{
    .wh(100%, @statusBar + @headHeight);
    background: @statusBarColor;
    position: relative;
    color: #282828;
    .statusBar{
      .wh(100%, @statusBar);
      background: @statusBarColor;
    }
    .nav-left{
      .wh-l(110px, @headHeight);
      float: left;
      position: relative;
      img{
        height: 42px;
        .f-center;
      }
    }
    .nav-center{
      .wh-l(calc(~"100% - 220px"), @headHeight);
      float: left;
      font-size: 36px;
      .activeColor{
        color: #282828;
      }
      ul{
        .wh(auto, 100%);
        margin: 0 auto;
        display:inline-block;
        li{
          position: relative;
          padding: 0 15px;
          img{
            width: 50px;
            position: absolute;
            left: 50%;
            margin-left: -25px;
            bottom: 9px;
          }
        }
        li:nth-child(1){
          .wh-f(auto, 100%);
        }
        li:nth-child(2){
          .wh-f(auto, 100%);
        }
        li:nth-child(3){
          .wh-f(auto, 100%);
        }
      }
    }
    .nav-right{
      .wh-l(110px, @headHeight);
      float: left;
      position: relative;
      img{
        height: 42px;
        left: 46% !important;
        .f-center;
      }
    }
  }
  .filterClass{
    filter: blur(10px);
  }
  .home-container{
    .fs-noTop;
    background: #FFF;
    overflow: hidden;
    .button{
      .wh(720px, 160px);
      position: absolute;
      left: 50%;
      margin-left: -360px;
      bottom: 10px;
      .btn{
        .wh(100%, 100%);
      }
      span{
        .wh-l(100%, 100px);
        position: absolute;
        left: 0;
        top: 20px;
        font-size: 30px;
        line-height: 110px;
        img{
          height: 38px;
          position: relative;
          left: 0;
          top: 10px;
          margin-right: 25px;
        }
      }
      .positioning{
        .wh(90px, 90px);
        position: absolute;
        left: 20px;
        top: -100px;
        img{
          height: 90px;
        }
      }
      .service{
        .wh(90px, 90px);
        position: absolute;
        right: 20px;
        top: -100px;
        img{
          height: 90px;
        }
      }
    }
  }
}
</style>



