<template>
  <div class="amap">
    <div id="container"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { MP } from '../../../../utils/amap.js'
export default {
  name: 'amap',
  props: {
    detailData: {
      type: Object,
      default: function () {
        return {}
      }
    },
    showDetail: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      geolocation: null, // 地理定位
      markers: [], // 点标记
      marksIcon1: require('../../../../assets/home_images/landmark.png'),
      marksIcon2: require('../../../../assets/home_images/baby.png'),
      marksIcon3: require('../../../../assets/home_images/charger.png'),
      marksIcon4: require('../../../../assets/home_images/location.png'),
      marksIcon5: require('../../../../assets/home_images/chargerBaby.png')
    }
  },

  computed: {
    ...mapGetters([
      'map', 'position'
    ])
  },

  mounted () {
    console.log(111)
    // 地图初始化
    MP('dd0ab4dec22fcb03c2e6a193fd42780d').then(AMap => {
      let timer = setTimeout(() => {
        this.initMap(AMap)
        clearTimeout(timer)
      }, 260)
    }).catch(err => {
      console.log(err)
    })
  },

  methods: {
    ...mapActions([
      'setPosition', 'createMap', 'setMapLanguage'
    ]),

    // 获取指定坐标2km范围内店铺位置返回
    storeList (res) {
      if (res.result && res.data) {
        // 显示全部点标志
        console.log('setMarker')
        res.data.data.map(item => {
          let icon
          switch (item.type) {
            case 1:
              icon = 'marksIcon2'
              break
            case 2:
              icon = 'marksIcon3'
              break
            case 3:
              icon = 'marksIcon5'
              break
          }
          this.setMarker(icon, item.m_lng, item.m_lat, item)
        })
      }
    },

    // 初始化地图
    initMap (AMap) {
      let layer = new AMap.TileLayer({
        opacity: 0.5 // 透明度
      })
      let map = new AMap.Map('container', {
        layers: [layer],
        lang: this.$i18n.locale === 'en' ? 'en' : 'zh_cn',
        resizeEnable: true, // 自适应大小
        center: [116.397428, 39.90923],
        // mapStyle: 'amap://styles/b981c7f090e14ad2340853d00242b4fa',
        // mapStyle: 'amap://styles/macaron',
        zoom: 13 // 初始视窗
      })
      // vuex创建地图
      this.createMap(map)
      // 点击地图事件监听
      this.clickHandler()
      this.map.plugin('AMap.Geolocation', () => {
        this.geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, // 是否使用高精度
          timeout: 10000,
          GeoLocationFirst: false,
          maximumAge: 0, // 定位结果缓存0毫秒，默认：0
          showButton: false,
          showMarker: false, // 定位成功后在定位到的位置显示点标记
          showCircle: false // 定位成功后用圆圈表示定位精度范围
          // zoomToAccuracy: true // 定位成功后调整地图视野范围使定位位置及精度范围视野内可见 (showCircle为true有效)
        })
        console.log(1)
        this.map.addControl(this.geolocation)
        this.geolocation.getCurrentPosition()
        console.log(2)
        AMap.event.addListener(this.geolocation, 'complete', (position) => {
          console.log((position))
          if (this.position === '') {
            // 设置地图中心点和缩放级别
            this.map.setZoomAndCenter(13, [position.position.lng, position.position.lat])
          }
          this.setMarker('marksIcon4', position.position.lng, position.position.lat)
          // 记录位置
          this.setPosition(position.position)
          console.log(this.position)
        }) // 返回定位信息
        AMap.event.addListener(this.geolocation, 'error', (message) => {
          let pos = '{"type":"complete","position":{"P":23.127799479167,"O":113.373349066841,"lng":113.373349,"lat":23.127799},"location_type":"html5","message":"Get geolocation success.Convert Success.Get address success.","accuracy":148,"isConverted":true,"status":1,"addressComponent":{"citycode":"020","adcode":"440106","businessAreas":[{"name":"天河公园","id":"440106","location":{"P":23.129927179487183,"O":113.36488121794878,"lng":113.364881,"lat":23.129927}},{"name":"棠下","id":"440106","location":{"P":23.12904394281045,"O":113.37899883986933,"lng":113.378999,"lat":23.129044}},{"name":"东圃","id":"440106","location":{"P":23.121330761883733,"O":113.40506492121466,"lng":113.405065,"lat":23.121331}}],"neighborhoodType":"","neighborhood":"","building":"海天楼","buildingType":"商务住宅;楼宇;商务写字楼","street":"建中路","streetNumber":"3号","province":"广东省","city":"广州市","district":"天河区","township":"天园街道"},"formattedAddress":"广东省广州市天河区天园街道海天楼广海大厦","roads":[],"crosses":[],"pois":[],"info":"SUCCESS"}'

          let position
          position = JSON.parse(pos)
          // 设置地图中心点和缩放级别
          this.map.setZoomAndCenter(13, [position.position.lng, position.position.lat])
          this.setMarker('marksIcon4', position.position.lng, position.position.lat)
          // 记录位置
          this.setPosition(position.position)

          console.log(message)
        }) // 返回定位出错信息
      })
      console.log(4)
      // 拖拽选址
      AMapUI.loadUI(['misc/PositionPicker'], (PositionPicker) => {
        var positionPicker = new PositionPicker({
          mode: 'dragMap',
          map: this.map,
          iconStyle: {
            url: this.marksIcon1,
            size: [15, 35], // 要显示的点大小，将缩放图片
            ancher: [7.5, 35] // 锚点的位置，即被size缩放之后，图片的什么位置作为选中的位置
          }
        })
        console.log(5)
        positionPicker.on('success', (positionResult) => {
          console.log(33333)
          // 清除所有标记点
          this.map.remove(this.markers)
          // 获取指定坐标2km范围内店铺位置请求
          this.dataInteractionRequest({
            msgType: '0x5283',
            lng: positionResult.position.lng,
            lat: positionResult.position.lat
          })
          console.log(positionResult.position)
          if (this.ISDEBUG) {
            let str = `{"msgType":"0x5284","result":true,"data":{"lng":0,"lat":0,"data":[{"m_lat":34.0540660399883,"merchant_id":101,"type":2,"m_lng":-118.245817205114},{"m_lat":34.048781588540415,"merchant_id":100,"type":1,"m_lng":-118.25940419910658}]}}`
            window.dataInteractionResponse(str)
          }
        })
        positionPicker.on('fail', (positionResult) => {
          console.log(44444)
          // 清除所有标记点
          this.map.remove(this.markers)
          // 获取指定坐标2km范围内店铺位置请求
          this.dataInteractionRequest({
            msgType: '0x5283',
            lng: this.position.lng,
            lat: this.position.lat
          })
          if (this.ISDEBUG) {
            let str = `'{"msgType":"0x5284","result":true,"data":{"lat":"23.128543","lng":"113.37454","data":[{"merchant_id":114,"m_lng":"113.367631","m_lat":"23.130691","type":1},{"merchant_id":115,"m_lng":"113.357297","m_lat":"23.125552","type":1}]}}'`
            window.dataInteractionResponse(str)
          }
        })
        positionPicker.start()
      })
    },

    // 定位控件
    setCenter () {
      if (this.position === '') {
        this.geolocation.getCurrentPosition()
      } else {
        this.map.panTo([this.position.lng, this.position.lat])
      }
    },

    // 点击地图事件
    clickHandler () {
      var clickHandler = (e) => {
        this.$emit('update:showDetail', false)
      }

      // 绑定事件
      this.map.on('click', clickHandler)
    },

    // 设置点标记 (marksIcon1 定位图标、marksIcon2 充电宝图标、marksIcon3 座充图标)
    setMarker (type, lng, lat, item) {
      // console.log(type, lng, lat)
      let w
      let h
      let size = {
        'marksIcon1': {w: 15, h: 35},
        'marksIcon2': {w: 39, h: 42.5},
        'marksIcon3': {w: 39, h: 42.5},
        'marksIcon4': {w: 20, h: 26.2},
        'marksIcon5': {w: 39, h: 42.5}
      }
      w = size[type].w
      h = size[type].h

      if (type === 'marksIcon2' || type === 'marksIcon3' || type === 'marksIcon5') {
        // 门店点标记
        var marker = new AMap.Marker({
          position: new AMap.LngLat(lng, lat),
          offset: new AMap.Pixel(-w / 2, -h),
          icon: new AMap.Icon({
            size: new AMap.Size(w, h), // 图标大小
            image: this[type],
            imageSize: new AMap.Size(w, h)
          }),
          map: this.map,
          zIndex: 100,
          extData: item,
          topWhenClick: true
        })
        marker.setMap(this.map)
        this.markers.push(marker)
        marker.on('click', (e) => {
          console.log(e.target.getExtData())
          let data = e.target.getExtData()
          // 查询店铺详情请求
          this.dataInteractionRequest({
            msgType: '0x5285',
            merchantId: data.merchant_id,
            lng: data.m_lng,
            lat: data.m_lat
          })
          console.log('1919919')
          console.log(data)
          console.log(data.m_lng, data.m_lat)
          if (this.ISDEBUG) {
            let name
            if (data.merchant_id === 100) {
              name = '亓行'
            } else if (data.merchant_id === 101) {
              name = '方圆e时光'
            } else {
              name = '网易'
            }
            let str = `{"msgType":"0x5286","result":true,"data":{"address":"广东省广州市天河区建中路3号","lng":113.367631,"distance":505451,"telephone":"13332965499","consumption":888,"type":1,"fullyCount":55,"banners":[],"vacancyCount":84,"merchantId":114,"name":"${name}","logo":"","openTime":"09:00-18:00","lat":23.130691}}`
            window.dataInteractionResponse(str)
          }
          // marker.markOnAMAP({
          //   name: '首开广场',
          //   position: marker.getPosition()
          // })
        })
      } else {
        // 中心点点标记
        marker = new AMap.Marker({
          position: new AMap.LngLat(lng, lat),
          offset: new AMap.Pixel(-w / 2, -h),
          icon: new AMap.Icon({
            size: new AMap.Size(w, h), // 图标大小
            image: this[type],
            imageSize: new AMap.Size(w, h)
          }),
          map: this.map,
          zIndex: 999
        })
        marker.setMap(this.map)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.amap{
  .fs;
  #container{
    .fs;
    .amap-logo {
      display: none !important;
    }
    .amap-copyright {
      bottom:-100px;
      display: none;
    }
  }
}
</style>



