<i18n>
{
  "en": {
    "NotLoggedIn": "Not logged in",
    "MyWallet": "My wallet",
    "TheOrder": "Order",
    "AboutUs": "About us",
    "Opinion": "Feedback",
    "CheckForUpdates": "Check for updates",
    "Language": "Language",
    "DiscoverNewVersions": "Discover new versions",
    "describe": "New version and updated content",
    "item": "Cancel | Confirm",
    "LatestVersion": "Latest version"
  },
  "zh": {
    "NotLoggedIn": "未登录",
    "MyWallet": "我的钱包",
    "TheOrder": "我的订单",
    "AboutUs": "关于我们",
    "Opinion": "意见反馈",
    "CheckForUpdates": "检查更新",
    "Language": "多语言设置",
    "DiscoverNewVersions": "发现新版本",
    "describe": "新版本更新内容",
    "item": "取消 | 确定",
    "LatestVersion": "已经是最新版本！"
  }
}
</i18n>

<template>
  <div class="sidebar">
    <transition name="delay">
      <div class="mask" v-show="sidebarShow" @click="openSidebar"></div>
    </transition>
    <transition name="fade">
      <div class="sidebar-container" v-show="sidebarShow">
        <div class="icon"
             v-if="isLogin"
             :style="{backgroundImage: 'url(' + userInfo.headPic + ')'}"
             @click="pageJump(1)">
        </div>
        <div v-else @click="pageJump(1)" class="default-icon"></div>
        <span @click="pageJump(1)" v-html="isLogin?userInfo.nickName:$t('NotLoggedIn')"></span>
        <ul class="list">
          <li  @click="pageJump(2)">
            <a>
              <img src="../../assets/sidebar_images/bag@3x.png" alt="">
              <strong>{{ $t('MyWallet') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(3)">
            <a>
              <img src="../../assets/sidebar_images/list@3x.png" alt="">
              <strong>{{ $t('TheOrder') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(4)">
            <a>
              <img src="../../assets/sidebar_images/aboutus@3x.png" alt="">
              <strong>{{ $t('AboutUs') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(5)">
            <a>
              <img src="../../assets/sidebar_images/idea@3x.png" alt="">
              <strong>{{ $t('Opinion') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(6)">
            <a>
              <img src="../../assets/sidebar_images/chink@3x.png" alt="">
              <strong>{{ $t('CheckForUpdates') }}</strong>
            </a>
          </li>
          <li  @click="pageJump(7)">
            <a>
              <img src="../../assets/sidebar_images/languagesetting@3x.png" alt="">
              <strong>{{ $t('Language') }}</strong>
            </a>
          </li>
        </ul>
      </div>
    </transition>

    <!-- 弹框 -->
    <message-box  :id="5"
                  icon="Update"
                  :title="$t('DiscoverNewVersions')"
                  :describe="$t('describe')"
                  :confirm="$tc('item',2)"
                  :cancel="$tc('item',1)"
                  @boxHandle="boxHandle"></message-box>
    <!-- toast -->
    <toast :toastTip.sync="toastbox"
           :describe="$t('LatestVersion')"
           position="bottom"></toast>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import messageBox from '../../components/MessageBox/index'
import toast from '../../components/Toast/index'
export default {
  name: 'sidebar',

  props: {
    sidebarShow: {
      type: Boolean,
      default: false
    }
  },

  components: {
    messageBox,
    toast
  },

  data () {
    return {
      toastbox: false,
      path: '',
      nickName: '', // 用户名
      headPic: '' // 头像
    }
  },

  mounted () {
    this.UPDATE_DATA()
    this.DATA_RESPONSE()
    console.log(1111)
  },

  activated () {
    this.UPDATE_DATA()
    console.log(2222)
  },

  computed: {
    ...mapGetters([
      'userInfo', 'isLogin'
    ])
  },

  methods: {
    ...mapActions([
      'setGlobalBox'
    ]),

    DATA_RESPONSE () {
      this.dataInteractionResponse(data => {
        // 检查是否有版本更新返回
        console.log('----------')
        if (data.msgType === '0x5022') {
          if (data.result) {
            if (data.data.upgrade) {
              console.log(6363)
              this.setGlobalBox({bol: true, id: 5})
              this.path = data.data.path
            } else {
              this.toastbox = true
            }
          }
        }
      })
    },

    UPDATE_DATA () {
      this.nickName = this.userInfo.nickName
      this.headPic = this.userInfo.headPic
    },

    // 关闭侧边栏
    openSidebar () {
      this.$emit('openSidebar', false)
    },

    boxHandle (confirm, cancel, id) {
      switch (id) {
        case 5:
          if (confirm) {
            // 版本更新请求
            this.dataInteractionRequest({
              msgType: '0x5023',
              path: this.path
            })
          }
          break
      }
      this.setGlobalBox({bol: false, id: 0})
    },

    // 跳转相应界面
    pageJump (num) {
      let page = {
        1: '/personal',
        2: '/wallet',
        3: '/order',
        4: '/about',
        5: '/feedback',
        7: '/language'
      }
      // 是否登录
      if (this.isLogin) {
        this.$router.push({path: page[num]})
      } else {
        if (num === 1 || num === 2 || num === 3) {
          this.$router.push('/login')
        } else {
          this.$router.push({path: page[num]})
        }
      }
      if (num === 6) {
        // 检查是否有版本更新请求
        this.dataInteractionRequest({
          msgType: '0x5021'
        })
        if (this.ISDEBUG) {
          let str = '{"msgType":"0x5022","result":true,"data":{"upgrade":true}}'
          window.dataInteractionResponse(str)
        }
      }
    }
  },

  watch: {
    sidebarShow (newValue) {
      if (newValue) {
        this.DATA_RESPONSE()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.sidebar{
  .mask{
    .fs;
    z-index: 10;
    background: rgba(0, 0, 0, .5)
  }
  .sidebar-container{
    .wh(550px, 100%);
    .fs;
    z-index: 10;
    background: #FFF;
    padding-top: @statusBar;
    .icon, .default-icon{
      .wh(220px, 220px);
      margin: 46px auto 0px auto;
      background: url('../../assets/personal_images/avatar.png') 0 0 no-repeat;
      background-size: cover;
      border-radius: 50%;
    }
    span{
      font-size: 35px;
      margin-top: 28px;
      color: #646464;
      display: block;
      height: 40px;
    }
    .list{
      .wh(100%, auto);
      margin-top: 108px;
      li{
        .wh-l(100%, 80px);
        position: relative;
        text-align: left;
        a{
          .wh(100%, 100%);
          display: block;
          img{
            height: 38px;
            position: absolute;
            left: 6%;
            top: 50%;
            margin-top: -22px;
          }
          strong{
            .wh(auto, 100%);
            font-size: 30px;
            display: block;
            margin-left: 17.5%;
          }
        }
      }
    }
  }
}

// .delay-leave-active{
//   transition-delay: .3s;
// }
.fade-enter-active,.fade-leave-active{
  transition: all .25s ease-out;
}
.fade-enter, .fade-leave-to{
  transform: translate3d(-100%, 0, 0);
  opacity: 0;
}
</style>



